<template>
  <div id="userManage">
    <QueryBox @searchSrldId="searchSrldId">
      <Button type="success" size="small" icon="md-add" class="btn-add" style="margin-right: 10px" @click.stop="$refs.userAdd.build = true">新建</Button>
    </QueryBox>
    <div class="tab_box">
      <Table ref="table" border stripe :loading="loading" :columns="columns1" :data="data1"  size="small" highlight-row :max-height="tableHeight"></Table>
    </div>
    <div class="page_box" id="pageBox">
      <Page :total="total" :page-size="pageSize" show-elevator @on-change="pageChange" size="small" show-total/>
    </div>
    <UserAdd ref="userAdd" :codeData="codeData" :departmentList="departmentList" @getTableData="getTableData"></UserAdd>
    <UserEdit ref="userEdit" :data="editDefaultData" :codeData="codeData" :departmentList="departmentList" @getTableData="getTableData"></UserEdit>
  </div>
</template>

<script>
import { sysMobileUserList, sysMobileUserDel, getDepartmentList, resetPassword } from '@/api/systemManage'
import { getMenu } from '@/api/acquisitionTable'
import { getToken } from '@/utils/auth'
import UserAdd from './User_add'
import UserEdit from './User_edit'
import QueryBox from './User_query'
export default {
  name: 'UserManage',
  data () {
    return {
      /* 查询条件 */
      code: '',
      limit: 10,
      realName: '',
      phoneNumber: '',
      department: '',
      /* 表格动态高度 */
      tableHeight: window.innerHeight - 290,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 页码 */
      pageNum: 1,
      /* 页面总条数 */
      total: 0,
      /* 重置密码数据id */
      resId: '',
      /* 删除数据id */
      delId: '',
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 表格 */
      columns1: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '姓名',
          key: 'realName'
        },
        {
          title: '用户手机号',
          key: 'phoneNumber'
        },
        {
          title: '行政区划',
          key: 'codeName'
        },
        {
          title: '所属部门',
          key: 'department'
        },
        {
          title: '操作',
          key: 'action',
          width: 270,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.editDefaultData = params.row
                    this.$refs.userEdit.edit = true
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-refresh'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.resId = params.row.userId
                    this.resetPassword()
                  }
                }
              }, '重置密码'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.delId = params.row.userId
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该用户么？',
                      onOk: () => {
                        this.sysDel()
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: [],
      /* 行政区划数据 */
      codeData: [],
      /* 部门列表 */
      departmentList: [],
      /* 加载 */
      loading: false
    }
  },
  components: {
    UserAdd,
    UserEdit,
    QueryBox
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
    }.bind(this)
    this.getTableData()
    this.getCodeMenu()
    this.getDepartmentList()
  },
  methods: {
    /* axios: 获取列表数据 */
    getTableData () {
      this.loading = true
      sysMobileUserList({
        accessToken: getToken(),
        validate: true,
        data: {
          page: this.pageSize * (this.pageNum - 1) + 1,
          limit: this.pageSize,
          realName: this.realName,
          phoneNumber: this.phoneNumber,
          code: this.code,
          department: this.department
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
          this.loading = false
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 重置密码 */
    resetPassword () {
      resetPassword({
        data: {
          id: this.resId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '密码成功重置为手机号后六位' })
        } else {
          this.$Notice.error({ title: '密码重置失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 删除数据 */
    sysDel () {
      sysMobileUserDel({
        accessToken: getToken(),
        validate: true,
        data: {
          userId: this.delId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 获取省市县菜单 */
    getCodeMenu () {
      getMenu({
        data: {
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.codeData = res.data.data
          this.codeData = this.valComCode(this.codeData)
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 省市县菜单value = comCode */
    valComCode (arr) {
      for (let i in arr) {
        arr[i].value = arr[i].comCode
        this.valComCode(arr[i].children)
      }
      return arr
    },
    /* axios: 获取部门列表 */
    getDepartmentList () {
      getDepartmentList({
        data: {
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.departmentList = res.data.data
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 查询框 */
    searchSrldId (obj) {
      this.code = obj.code
      this.department = obj.department
      this.phoneNumber = obj.phoneNumber
      this.realName = obj.realName
      this.getTableData()
    },
    /* 分页跳转 */
    pageChange (p) {
      this.page = p
    }
  }
}
</script>

<style scoped lang="scss">
#userManage{
  .from_box{
    padding:10px;
    text-align: left;
  }
  .tab_box{
    padding: 0 15px;
  }
  .page_box{
    width: 100%;
    padding: 15px;
    text-align: right
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    margin-left: 10px;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
}
</style>
